require("style/main.scss");
require("style/filter.scss");
require("style/reset.scss");
require("style/ZoomControls.scss");

var Config = require("./core/Config");

require(["domready"], function(domReady){

	domReady(function(){

		document.onselectstart = function () { return false; };
		require(["Birds"], function(Birds){
			var birdsElement = document.getElementById("birds");
			var aboutButton = document.getElementById("aboutLink");
			var startButton = document.getElementById("startLink");

			var toggleFullScreen = function() {
				if (!document.mozFullScreen && !document.webkitFullScreen) {
					if (birdsElement.mozRequestFullScreen) {
						birdsElement.mozRequestFullScreen();
					} else {
						birdsElement.webkitRequestFullScreen(Element.ALLOW_KEYBOARD_INPUT);
					}
				} else {
					if (document.mozCancelFullScreen) {
						document.mozCancelFullScreen();
					} else {
						document.webkitCancelFullScreen();
					}
				}
			};

			var onAbout = function(){
				event.preventDefault();
				event.stopPropagation();
			};

			var onBlank = function(){
				event.preventDefault();
				event.stopPropagation();
			};

			var legal = document.getElementById("legal");
			var privacy = legal.getElementsByTagName("a")[0];
			var terms = legal.getElementsByTagName("a")[1];
			var badges = document.getElementById("badges");
			var aiExperiment = badges.getElementsByClassName("badge")[0];
			var friends = badges.getElementsByClassName("badge")[1];

			var onStart = function(){
				trace("onStart");
				startButton.removeEventListener('mouseup', onStart, false);
				document.body.removeEventListener('mouseup', onStart, false);
				aboutButton.removeEventListener('mouseup', onAbout, false);
				aboutButton.removeEventListener('touchend', onAbout, false);

				privacy.removeEventListener('mouseup', onBlank, false);
				privacy.removeEventListener('touchend', onBlank, false);
				terms.removeEventListener('mouseup', onBlank, false);
				terms.removeEventListener('touchend', onBlank, false);
				aiExperiment.removeEventListener('mouseup', onBlank, false);
				aiExperiment.removeEventListener('touchend', onBlank, false);
				friends.removeEventListener('mouseup', onBlank, false);
				friends.removeEventListener('touchend', onBlank, false);
				
				birds.beginExperience();
				event.preventDefault();
				event.stopPropagation();
			};

			var onFullScreenStart = function(){
				trace("onFullScreenStart");
				toggleFullScreen();
				birds.beginExperience();
				event.preventDefault();
				event.stopPropagation();
			};

			var birds = new Birds();
			birds.addEventListener("BIRDS_LOADED",function(){
				trace("BIRDS_LOADED");

				if(!Config.isSplashDisabled){
					aboutButton.addEventListener('mouseup', onAbout, false);
					aboutButton.addEventListener('touchend', onAbout, false);

					privacy.addEventListener('mouseup', onBlank, false);
					privacy.addEventListener('touchend', onBlank, false);
					terms.addEventListener('mouseup', onBlank, false);
					terms.addEventListener('touchend', onBlank, false);
					aiExperiment.addEventListener('mouseup', onBlank, false);
					aiExperiment.addEventListener('touchend', onBlank, false);
					friends.addEventListener('mouseup', onBlank, false);
					friends.addEventListener('touchend', onBlank, false);

					startButton.addEventListener('mouseup', onStart, false);
					document.body.addEventListener('mouseup', onStart, false);
				} else {
					birds.beginExperience();
				}
			});
			birds.init();

		});

	});

});


